import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { ExchangeVoucherComponent } from './exchange-voucher.component';
import { RouterModule, Routes } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { NgZorroAntModule } from 'src/app/ng-zorro-ant.module';
import { TranslateModule } from '@ngx-translate/core';
import { IonicModule } from '@ionic/angular';
import { LottieModule } from 'ngx-lottie';
import { playerFactory } from 'src/app/app.module';
import { ComponentsModule } from 'src/app/sales/sale-desktop/components/components.module';
import { AccountInformationComponent } from './account-information/account-information.component';
import { ConfigPrintComponent } from './config-print/config-print.component';

const routes: Routes = [
  {
    path: '',
    component: ExchangeVoucherComponent,
  }
]


@NgModule({
  declarations: [
    ExchangeVoucherComponent,ConfigPrintComponent,AccountInformationComponent
  ],
  imports: [
    CommonModule,
    ReactiveFormsModule,
    FormsModule,
    NgZorroAntModule,
    TranslateModule,
    ComponentsModule,
    IonicModule,
    RouterModule.forChild(routes),
    LottieModule.forRoot({ player: playerFactory }),
  ]
})
export class ExchangeVoucherModule { }
